@import "config";
@import "core";
.color--primary {
	color: color(primary);
}
.color--secondary {
	color: color(secondary);
}
.color--tetriary {
	color: color(tetriary);
}
.color--accent {
	color: color(accent);
}
.color--grey-light{
	color: color(grey-light);
}
.color--grey-accent{
	color: color(grey-accent);
}

.color--white{
	color: color(white);
}
.bg--secondary {
	background: color(secondary);
}
h1 {
	font-size: font-size(h1);
}
h2 {
	font-size: font-size(h2);
}
h3 {
	font-size: font-size(h3);
}
a {
	text-decoration: none;
	color: none;
}
main {
	max-width: $page-maxwidth;
	margin: 0 auto;
}
.wrapper-col {
	width:100%;
	display:flex;
	flex-direction:column;
	align-items:center;
}
.wrapper-row {
	display: block;
}
/*------------------------ Header -------------------------*/
#banner {
	z-index: -10;
	background: color(accent) url("../images/banner_mobile_light.jpg") no-repeat center;
	background-size: cover;
	min-height: 500px;
	width: 100%;
}
.container--hero {
	@extend .container;
	@extend .col;
	color: color(grey-dark);
	align-items: center;
	justify-content: center;
	min-height: 500px;
	padding-top: 10vh;

	.hero__text {
		padding: 5px 10px 5px 10px;
		text-align: center;
	}
	.header--accent--dark {
		color: color(white);
		font-weight: bold;
		text-transform: uppercase;
	}
	img {
		max-width: 140px;
	}
}
.header__span {
	color: color(white);
	font-weight: bold;
	text-transform: uppercase;
	text-shadow: 1px 1px black;
}

.btn {
	font-size: font-size(button);
	border: 1px solid white;
	border-radius: 3px;
	width: 200px;
	height: 50px;
	padding: 12px 22px 12px 22px;
	margin-bottom: 20px;
}
.btn--secondary {
	background: rgba(0,0,0,.2);
	color: white;
}
.btn--primary{
	border: none;
	background: color(primary);
	color: white;
}
.btn--primary:hover {
	background: color(btnphover);
	border: 1px solid color(primary);
}
.btn--secondary:hover {
	background: rgba(0,0,0,.5);
	color: white;
}
/*------------------------ Header Cards -------------------------*/
.container--top-cards {
	@extend .container;
	@extend .col;
	flex-wrap: wrap;
	background: color(grey-dark);
}
.top-card {
	@extend .row;
	min-height: 150px;
	color: white;
	align-items: center;
	justify-content: left;
	border-bottom: 1px solid color(accent);
	padding: 10px 10px 10px 30px;
}
.top-card__icon {
	color: color(accent);
	font-size: 50px;
	img {
		display:inline-block;
		height:2.5rem;
		width: 2.5rem;
	}
}
.top-card__text {
	padding: 30px;
	color: white;
	text-align: left;
	font-size: font-size(text);
	line-height: 1.5;
	}
.top-card__text__phone-number {
		font-weight:bold;
		font-size:2rem;
		color: color(white);
	}

/*------------------------ Intro -------------------------*/
.container--intro {
	@extend .container;
	@extend .col;
	background:color(white);
	align-items:center;
}

.content-wrapper--intro {
	@extend .col;
	max-width: 1000px;
	align-items:right;
	padding:1rem
}
.wrapper-col-to-row-at-d{
	@extend .col;
}
.wrapper-col-to-row-at-t{
	@extend .col;
}

.wrapper--intro-card--100 {
}
.intro-card {
	margin: 0rem;
}
.intro-card__text {
	color: color(grey-light);
	font-weight: bold;
	font-size: font-size(text);
	line-height: 1.5;
	padding: 0 0 2rem 0;
}
.wrapper--intro-card--qualities {
	@extend .row;
	flex-wrap:wrap;
	border-top:1px solid black;
	padding: 4rem 0 4rem 0;
}
.intro-card--qualities {
	@extend .col;
	color: color(grey-dark);
	font-family: serif;
	max-width: 320px;
	padding-bottom: 3rem;
	&__title {
		@extend .row;
		justify-content: flex-start;
		align-items: center;
		color: color(tetriary);
		img {
			width: 40px;
			height: 40px;
			margin-right: 20px;
		}
	}
	&--qualities__text {
		color: color(grey-dark);
		margin-bottom: 20px;
		font-size: font-size(text);
	}
}
/*------------------------ Services -------------------------*/
.container--services {
	@extend .container;
	background: linear-gradient(color(accent), color(very-light));
	padding-top:20px;
	h1 {
		padding: 0px 10px 10px 0px;
		margin-top: 0;
	}
}
.content-wrapper--services{
	@extend .col;
	justify-content:space-between;
	}
.services-card {
	color: color(primary);
	display: flex;
	flex-direction: column;
	padding: 30px 30px 30px 30px ;
	h2 {
		padding: 5px;
		margin: 0px;
	}
	&__img {
		width: 16*17px;
		height: 9*17px;
		box-shadow: 10 10 10;
		clip-path: polygon(0 0, 95% 1%, 100% 100%, 5% 100%);
	}
	align-items: center;
	&__text {
		color: color(primary);
		font-weight: bold;
	}
}
.services-btn {
	transform: translate(0, -20px);
}
/*------------------------ Reviews -------------------------*/
.container--reviews {
	background:url("../images/review_bg_mobile_dark.jpg") no-repeat;
	background-size: cover;
	padding-bottom: 40px;
}
.review__header {
	padding: 1rem;
	color: color(white);
}
.review__block {
	min-width: 270px;
	margin: 4rem 1rem 4rem 1rem;
}
.review__text {
	padding-bottom: 10px;
	font-family: serif;
	max-width: 55ch;
	font-size: font-size(text);
	font-style: italic;
	color: color(white);
	font-weight: 500;
}
.review__star {
	width:1rem;
}
.review__quote {
	color: color(white);
	padding-bottom: 3px;
}
.review__name {
	color: color(white);
	font-weight: bold;
}
.review__platform {
	color: color(white);
	font-size: 14px;
	font-style: italic;
}
/*------------------------ Porch -------------------------*/
.container--porch {
	@extend .col;
	background: color(grey-light);

}
.container--porch__wrapper {
	@extend .container;
	@extend .row;
	align-items: center;
	background: color(grey-light);
	justify-content: space-evenly;
	height: 220px;
	a {
		transform: translate(0, -80px);
		margin-top:100px;
	}
}

.porch-badge {
	width: 50px;
	padding:5px 1vw 5px 1vw;
}
.porch-badge--sm {
	@extend .porch-badge;
	display: none;
	width: 25px;
}
.porch__text {
	font-family: serif;
	font-style: italic;
	color: white;
	padding-bottom: 10px;
}
/*------------------------ About -------------------------*/
.container--about {
	@extend .container;
	@extend .row;
	background: white;
	justify-content: center;
}
.about__text {
	color: color(grey-dark);
	max-width: 500px;
	font-family: serif;
	font-style: italic;
	margin-bottom: 40px;
	font-size: font-size(text);
	line-height: 1.5;
}
.about__card {
	@extend .col;
	margin:30px;
}
/*------------------------ Portfolio -------------------------*/
.container--gallery {
	@extend .container;
	@extend .row;
	background: color(accent);
	flex-wrap: wrap;
	justify-content: center;
}
.gallery__img {
	width: 100%;
	max-width: 500px;
	height: 200px;
	object-fit: cover;
	margin: 1px;
}
/*------------------------ Bottom-Form -------------------------*/
.container--bottom-form {
	@extend .container;
	@extend .row;
	justify-content: space-evenly;
	flex-wrap: wrap;
	background:white;
}
.form-wrapper {
	margin-bottom:30px;
	max-width: 290px;
	padding: 10px;
	h3 {
		padding-left: 10px;
		color: color(accent);
	}
	form {
		padding: 10px;
		background: linear-gradient(color(secondary), color(tetriary));
		input, textarea {
			border: none;
			width: 250px;
			padding: 15px 15px 15px 5px;
			margin-bottom: 15px;
			font-size: font-size(text);
		}
		label {
			color: color(primary);
			display: block;
			width: 100%;
			padding: 5px;
			font-weight: bold;
			font-size: font-size(text);
		}
	}
}
.bottom-form--info__wrapper {
	margin-bottom:30px;
	min-width: 200px;
	max-width: 290px;
	padding: 10px;
	font-size: font-size(text);
	display:flex;
	flex-direction: column;
	justify-content:space-around;
	h3 {
		color: color(secondary);
	}
	p {
		color: color(grey-accent);
	}
	span {
		font-weight: bold;
		color: color(tetriary);
	}
	&__text {
		padding: 10px;
		margin-bottom: 80px;
	}
	&__phone {
		padding-left: 10px;
	}
}

@mixin general--tablet() {
	.color1 {
	}
	.wrapper-row--services {

	}
	.porch-badge--sm {
		display: block;
	}
	.container--top-cards {
		display:flex;
		flex-direction:row;
		flex-wrap:nowrap;
		justify-content:center;
		padding:1rem;
	}
	.top-card{
		display: flex;
		flex-direction:column;
		border-bottom: none;
		border-left: 1px solid color(accent);
		border-right: 1px solid color(accent);
		padding: .5rem;

	}
		.wrapper-col-to-row-at-t{
		display:flex;
		flex-direction:row;
		flex-wrap:wrap;
		justify-content:space-between
}
}
@mixin general--desktop () {
	.wrapper-row {
		display: flex;
		flex-wrap:wrap;
	}
	.wrapper--intro-card--100 {
		padding-right:4rem;
		justify-content: space-between;
	}
	.wrapper--intro-card--qualities {
		display:flex;
		flex-direction:column;
		flex-wrap:nowrap;
		justify-content: space-between;
		border:none;
		border-left: 1px solid black;
		padding-left:4rem;
	}
	.wrapper-col-to-row-at-d{
		display:flex;
		flex-direction:row;
		flex-wrap:wrap;
		justify-content:space-between
	}
	.content-wrapper--intro{
		padding-top:10rem;
	}
	.container--top-cards {
		max-width:1000px;
		margin: auto;
		transform: translate(0, 80px);
	}
	.intro-card {
		margin: 4rem 0 4rem 0;
	}
	.services-card {
		padding: 2rem 6rem 2rem 6rem;
	}
	.top-card {
		display:flex;
		flex-direction:row;
		padding-left: 2rem;
	}
	.container--reviews {
		background: url("../images/review_bg_desktop.jpg");
		background-attachment:fixed;
		background-size:cover;
		background-repeat:no-repeat;
	}
	#banner {
		background-attachment:fixed;
	}

}
