@import "config";
@import "core";
@import "general";

footer {
	max-width: $page-maxwidth;
	margin: 0 auto;
}
#footer-container-top {
	@extend .col;
		flex-wrap: no-wrap;
	justify-content:center;
	background:color(grey-light);
	/*	align-content: stretch; */
	/*	align-items: stretch; */
	/*	padding-bottom: 30px; */
}
.footer-wrapper {
	@extend .col;
	min-width: 200px;
	padding:7px 0px 7px 0px;
	flex: 1;
	border-bottom: 1px solid black;
	margin: 0 1rem .5rem 1rem;
	align-items:center;

}
.footer-titles__wrapper {
	@extend .col;
	justify-content: left;
}
.footer-titles {
	font-weight: bold;
	color: color(accent);
}
.footer-titles__divider {
	width: 40px;
	height: 4px;
	background: color(accent);
	margin:-.5rem 0 1rem 0;
}
#footer-social {
	@extend .footer-wrapper;
}
.container--gallery--footer {
	@extend .container--gallery;
	background: none;
	a {
		margin:3px;
	clip-path: circle(46% at 30px 30px);
	}
}
.footer-gallery__img {
	max-width: 60px;
	clip-path: circle(50%);

}
#footer-map {
	@extend .footer-wrapper;
}
.map-title {
	padding-left: 2rem;
}
.footer-link-wrapper {
	@extend .row;
	flex-wrap: wrap;
}
.footer-link-block {
	line-height: 2.5;
	font-size: 1.1rem;
	padding: 0 2rem 0 2rem;
	text-transform: capitalize;
}
.footer-link {
	color: color(white);
	font-weight:bold;
	text-decoration:underline;
}
.footer-link:hover {
	color: color(accent);
}
#footer-contact {
	@extend .footer-wrapper;
	a {
		color: color(white);
		font-weight: bold;	
	}
}
.footer-contact__content {
	@extend .col;
	justify-content: left;
}
#footer-copyright {
	@extend .footer-wrapper;
	font-size: small;
	background: #222;
	color: white;
	padding: 10px;
}
@mixin footer--desktop() {
	#footer-container-top {
		display:flex;
		flex-direction: row;
		justify-content:center;
		align-content: stretch;
		align-items: stretch;
	}
	#footer-map > .footer-titles__wrapper {
		padding-left: 2rem;
	}

}

