@import "config";
@import "core";

/*------------------------ Header -------------------------*/
.subpage__header {
	background: color(white);
	padding: 10vh 1rem 10vh 1rem;
	border-bottom: 1px solid color(grey-accent);
	h1 {
		color: color(secondary);
	}
}
.location {
	color: color(grey-accent);
	font-weight: bold;
	a {
		color: color(secondary);
		text-decoration: underline;
	}
}
/*------------------------ Content -------------------------*/
.subpage__container {
	@extend .col;
	align-items:center;
}
.subpage__col-wrapper {
	@extend .col;
	align-items:center;
	padding: 1rem;
	background: url('/assets/images/circle.svg') no-repeat;
	background-position: 50%;
	background-size:cover;
	margin-bottom:2rem;
	/*	width: 900px; */
}
.subpage__row-wrapper {
	/*	@extend .row;*/
	/*	flex-wrap: wrap;*/
	/*	justify-content: space-between;*/
	width: 100%;
	padding: 4rem 1rem 4rem 1rem;
}
.subpage__text {
	max-width: 300px;
	color: color(grey-light);
	line-height: 1.5;
	font-size: 1.25rem;
	background: rgba(255,255,255,.7);
	h2 {
		color:color(tetriary);
		margin: 0;
	}
}
.subpage__img {
	max-width: 300px;
	height: 150px;
	padding: 0 0 1rem 0;
clip-path: polygon(0 0, 90% 0, 100% 100%, 10% 100%);
}
.mobile-order1 {
	order: 1;
}
/*------------------------ Reviews -------------------------*/
.subpage-review {
	@extend .col;
	justify-content: center;
	align-items:center;
	padding: 4rem 1rem 4rem 1rem;
	/*	background: url('/assets/images/gallery_new-braunfels-texas_interior.jpg') no-repeat; */
	/*	background-attachment: fixed; */
	/*	background-size:cover; */
	background: linear-gradient(color(very-light), color(accent));
}
.review-card {
	border-radius:4px;
	order: 1px solid color(grey-accent);
	margin: 1rem auto 2rem;
	padding:1rem;
	box-shadow:10px 5px  9px color(grey-accent); 
	max-width: 500px;
	background: color(white);
}
.subpage-review__text{
	padding-bottom: 10px;
	font-family: serif;
	max-width: 50ch;
	font-size: font-size(text);
	font-style: italic;
	font-weight: 500;
	line-height: 1.5;
}
.subpage-review__name {
	font-weight:bold;
	color: color(grey-accent);
}
.subpage-review__platform {
	font-style: italic;
}
.subpage-button__wrapper {
	@extend .col;
	align-items:center;
	padding: 4rem 1rem 4rem 1rem;
	background: url('/assets/images/stripe.svg') no-repeat;
	background-position: center;
	background-size: 100% 100%;
}
/*------------------------ Button -------------------------*/
#estimate-button{
	padding-top:1rem;
	background: linear-gradient(color(accent), color(very-light));
	}
@mixin subpage--tablet() {
	.mobile-order1 {
		order: 0;
	}
	.subpage__img {
		width: 500px;
	}
	.subpage__row-wrapper {
		width: 80vw;
	}
	.subpage__col-wrapper {
		background: url('/assets/images/circle.svg') no-repeat;
		background-position: right;
		background-size:contain;
	}
}
/*------------------------ Mixin -------------------------*/
@mixin subpage--desktop(){
	.subpage__row-wrapper {
		width: 900px;
	}
	.subpage__header {
		padding-left: 8rem;
	}
}
