@import "normalize";
@import "core";
@import "nav";
@import "general";
@import "footer";
@import "subpage";

/* phone-small is default */
$phone-big: 576px;
$phone--hz:768px; /*keep? */
$tablet:768px;
$desktop: 992px;
$l--desktop: 1200px;
$xl--desktop: 1800px;

@media (min-width: #{$tablet}) {
	@include general--tablet()
	@include subpage--tablet()

}
@media (min-width: #{$desktop}) {
	@include general--desktop()
	@include footer--desktop()
	@include subpage--desktop()
	@include nav--desktop()
}


