@import "config";

.navbar {
	z-index: 10;
	background: color(grey-dark);
	position: fixed;
	top: 0px;
	left: 0px;
	right: 0px;
	max-width: $page-maxwidth;
	margin: 0 auto;
	display:flex;
	flex-direction:row;
	transition:padding 300ms ease-in-out;
	padding: .5rem 0 .5rem 0;
}
.navbar__logo {
	@extend .col;
	justify-content: center;
	padding: 0 0 0 .5rem;
	img{
	max-height:2rem;
}
}
.navbar__brand-name {
	font-size:1.5rem;
	font-weight:bold;
	padding: .5rem;
	color: color(white);
	display:flex;
	flex-direction:column;
	justify-content:center;
}
.navbar__list {
	background: color(grey-dark);
	padding: 0;
	margin:0;
	list-style:none;
	text-align: left;
	width: 100%;
	position:absolute;
	top:98%;
	left: 0;
	display:flex;
	flex-direction:column;
	align-items: left;
	padding-left: 25%;
	transform: scale(1, 0);
	transition: transform 400ms ease-in-out;
	transform-origin: top;
	li {
		font-size: 1.25rem;
		margin: 0 0 1rem 0;
		text-transform: capitalize;
		a {
			color: color(white);
			opacity: 0;
			transition: opacity 150ms ease-in-out;
			text-decoration:none;
			&.current {
				color:color(accent);
			text-decoration:underline;
			}
		}

		a:hover {
			color: color(secondary);
		}
	}
	li:hover {
		color: color(white);
	}
}
.navbar__toggle {
	display: none;
}
.navbar__toggle:checked ~ .navbar__list {
	transform: scale(1,1);
	li a {
		opacity: 1;
		transition: opacity 150ms ease-in-out 150ms;
	}
}
.navbar-toggle-label {
	color:white;
	position: absolute;
	right: 1rem;
	top: .1rem;
	height: 100%;
	display:flex;
	align-items: center;
}
.navbar-toggle-label span,
.navbar-toggle-label span::before,
.navbar-toggle-label span::after {
	display:block;
	background: color(white);
	border-radius: 2px;
	height: 2px;
	width: 30px;
	position:relative;
}
.navbar-toggle-label span::before,
.navbar-toggle-label span::after {
	content: '';
	position: absolute;
}
.navbar-toggle-label span::before {
	bottom:	7px;
}
.navbar-toggle-label span::after {
	top:7px;
}
@mixin nav--desktop() {
	.navbar {
		height: 4rem;
	}
	.navbar-toggle-label {
		display: none;
	}
	.navbar__brand-name {
		width: 20rem;
	}
	.navbar__list {
		transform:none;
		transition: none;
		position: static;
		flex-direction: row;
		flex-wrap:nowrap;
		justify-content: space-between;
		align-items:center;
		padding: .25rem 3rem .25rem 3rem;
		li {
			padding:0;
			margin: 0;
			a {
			transform: none;
			opacity: 1;
			}
		}
	}
}
