$backgroundColor: #212121;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

$colors: (
	dark: black,
	primary: hsla(210,94,10,1),
	secondary: hsla(210,94,54,1),
	tetriary: hsla(210,94,35,1),
	accent: hsla(210,94,80,1),
	very-light: hsla(210,94,96,1),
	error: red,
	grey-dark: #212121,
	grey-light: #333333,
	grey-accent: #636363,
	neutral: #212121,
	white: #fff,
	btnphover: hsla(210,94,30,1),
	orange: #D65800,
);
$font-sizes: (
	h1: 2rem,
	h2: 1.5rem,
	h3: 1.25rem,
	text: 1rem,
	button: 1.25rem,
);
$page-maxwidth: 1210px;
@function color($color-name) {
	@return map-get($colors, $color-name);
};

@function font-size($font-name) {
	@return map-get($font-sizes, $font-name);
};

